.footer {
    // .footer__content
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding-top: 60px;
    }
    // .footer__logo
    &__logo {
        width: 150px;
        height: auto;
    }
    // .footer__nav
    &__nav {
    }
    // .footer__list
    &__list {
        display: flex;
        align-items: center;
        gap: 20px;
    }
    // .footer__item
    &__item {
    }
    // .footer__link
    &__link {
        span {
            font-size: 12px;
        }
    }
    // .footer__address
    &__address {
        font-size: 12px;
        display: flex;
        justify-content: space-evenly;

        margin-top: 15px;

        a {
            display: flex;
            gap: 10px;
            align-items: center;
            margin-top: 10px;
            margin-left: -33px;
            font-weight: 500;
        }
    }
    // .footer__copyright
    &__copyright {
        font-size: 11px;
    }
}
