/* Стили для textarea */
.textarea {
    background-color: #fff;
    border: none;
    border-radius: 20px;
    width: 100%;
    padding: 23px 0;
    padding-left: 46.5px;
    color: #333; /* Замените на вашу переменную $text */
    font-size: 16px;
    border: 2px solid transparent;
    transition: 0.25s ease-in-out all;
    resize: none; /* Позволяет изменять размер textarea только по вертикали */
    min-height: 200px; /* Минимальная высота textarea */
}

.textarea::placeholder {
    font-size: 16px;
    color: #888; /* Замените на вашу переменную $text2 */
    transition: 0.25s ease-in-out all;
}

.textarea.error {
    border: 2px solid #c62429; /* Замените на вашу переменную $primary-color */

    &::placeholder {
        color: #c62429; /* Замените на вашу переменную $primary-color */
    }
}
