.btn {
    display: inline-block;
    background-color: transparent;
    text-align: center;
    cursor: pointer;
    transition: 0.25s ease-in-out all;
    border: none;
    cursor: pointer;

    &-appoitment {
        padding: 22px 0;
        font-size: 13px;
        border: 1px solid $primary-color;
        border-radius: 100px;
        min-width: 220px;

        background: {
            image: linear-gradient(45deg, $primary-color 50%, transparent 50%);
            position: 100%;
            size: 250%;
        }
        transition: all 0.3s ease-in-out;

        &:hover {
            background-position: 0;
            color: #fff;
        }
    }

    &-primary {
        padding: 30px 35px;
        border-radius: 100px;

        box-shadow: 0 0 10px -4px rgba(255, 255, 255, 0.3);
        position: relative;
        z-index: 5;
        transition: 0.25s ease-in-out all;

        span {
            position: relative;
            z-index: 15;

            transition: 0.25s ease-in-out all;

            color: #fff;
            font-size: 13px;
            line-height: 150%;
            font-weight: 600;
            letter-spacing: 0.15em;
            text-transform: uppercase;
        }
    }

    // ._rot
    &._rot {
        border: 2px solid $primary-color;
        background: $primary-color;

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            width: 90%;
            left: 5%;
            width: 269px;
            height: 67px;
            filter: blur(14px);
            z-index: 10;
            background-color: $primary-color;
            transition: 0.25s ease-in-out all;
        }

        &:hover {
            &::after {
                opacity: 0;
            }
            span {
                color: #000;
            }

            background-color: #fff;
        }

        &._hover2:hover {
            background-color: #b02025;

            &::after {
                opacity: 0;
            }
            span {
                color: #fff;
            }
        }
    }
    // ._black
    &._black {
        border: 2px solid $secondary-color;
        background: $secondary-color;

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            width: 90%;
            left: 5%;
            width: 269px;
            height: 67px;
            filter: blur(14px);
            z-index: 10;
            background-color: $secondary-color;
            transition: 0.25s ease-in-out all;
        }

        &:hover {
            &::after {
                opacity: 0;
            }
            span {
                color: #000;
            }

            background-color: #fff;
        }
    }

    &._success {
        border: 2px solid $success-color !important;
        background: $success-color !important;

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            width: 90%;
            left: 5%;
            width: 269px;
            height: 67px;
            filter: blur(14px);
            z-index: 10;
            background-color: $success-color !important;
            transition: 0.25s ease-in-out all;
        }

        &:hover {
        }
    }
}
