.hero {
    height: 70vh;
    display: flex;
    align-items: center;
    width: 100%;
    &__content {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    // .hero__links
    &__links {
    }
    // .hero__title
    &__title {
        font-size: 24px;
        line-height: 150%;
        max-width: 600px;

        margin-bottom: 20px;

        span {
            font-size: 18px;
            line-height: 150%;
            font-weight: 600;
            padding-left: 65px;

            position: relative;

            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 13px;
                transform: translateY(-50%);

                width: 40px;
                height: 2.85px;
                background: $primary-color;
            }
        }
    }
    // .hero__subtitle
    &__subtitle {
        margin-bottom: 35px;
        font-size: 18px;
        line-height: 150%;
    }
    // .hero__group-btn
    &__group-btn {
        display: flex;
        gap: 15px;
    }
    // .hero__right
    &__right {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 50px;
    }
    &__number {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 7px;
    }

    &__number-text {
        font-size: 15px;
        text-align: center;
        line-height: 140%;
        text-decoration: underline;

        span {
            color: $primary-color;
            text-decoration: underline;
        }
    }
}

.scroll-elem {
    display: flex;
    justify-content: center;
    align-items: center;
}
