@import "variables";
@import "fonts";
@import "_vendor/reset";
@import "components/all";

* {
	box-sizing: border-box;
}

body {
	background-color: #fff;
	font-family: "Roboto";
	font-weight: 400;
	color: $text;

	position: relative;

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;

		width: 100%;
		height: 825px;

		background: url(../assets/img/main-bg.png) center center no-repeat;
	}

	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		top: 3400px;
		left: 0;

		width: 100%;
		height: 2200px;

		transform: rotate(180deg);

		background: url(../assets/img/main-bg2.png) center center no-repeat;
	}
}

.container {
	max-width: $container-width;
	padding: 0px 15px;
	margin: 0 auto;
}

#root {
	padding-bottom: 50px;
	scrollbar-width: none;
}

body::-webkit-scrollbar {
	width: 0; /* For Chrome, Safari, Opera */
}
