.input {
    background-color: #fff;
    border: none;
    border-radius: 100px;
    width: 100%;
    padding: 23px 0;
    padding-left: 46.5px;
    color: $text;
    font-size: 16px;

    border: 2px solid transparent;

    transition: 0.25s ease-in-out all;

    &::placeholder {
        font-size: 16px;
        color: $text2;
        transition: 0.25s ease-in-out all;
    }

    &.error {
        border: 2px solid $primary-color;

        &::placeholder {
            color: $primary-color;
        }
    }
}
