.hero-swiper {
    width: 500px;
    height: 300px;
}

.hero-swiper-slide {
    background-position: center;
    background-size: cover;
    overflow: hidden;
    border-radius: 30px;
}
.hero-swiper-slide img {
    overflow: hidden;
    border-radius: 30px;
    display: block;
    width: 100%;
}
