.service {
    display: flex;
    align-items: center;
    flex-direction: column;

    // .service__content
    &__content {
        display: flex;
        gap: 10px;
        align-items: center;
    }
    &__title {
        text-align: center;
        font-size: 30px;
        max-width: 810px;
        line-height: 150%;

        padding-top: 40px;
    }
    &__subtitle {
        text-align: center;
        font-size: 22px;
        max-width: 550px;
        line-height: 150%;
        margin-bottom: 40px;
        color: $text2;
    }
    // .service__item
    &__item {
        // background img to all items
        &:nth-child(1) {
            background: url(../../assets/img/service/01.png) right bottom no-repeat;
        }
        &:nth-child(2) {
            background: url(../../assets/img/service/02.png) right bottom no-repeat;
        }
        &:nth-child(3) {
            background: url(../../assets/img/service/03.png) right bottom no-repeat;
        }
        &:nth-child(4) {
            background: url(../../assets/img/service/04.png) right bottom no-repeat;
        }

        border-radius: 30px;
        flex: 1 1 25%;
        height: 150px;
        padding-left: 27px;
        padding-right: 100px;

        display: flex;
        align-items: center;
        color: #fff;
        font-size: 15px;
        line-height: 140%;
        // ._red
        &._red {
            background-color: $primary-color;
        }
        // ._black
        &._black {
            background-color: $secondary-color;
        }
    }
    // .service__text
    &__text {
    }
}
